<template>
  <div>
    <el-card class="box-card animated fadeInLeft">
      <div class="text item">
        <el-row :gutter="20">
  <el-col :span="6">
    <img class="avator" :src="url" alt="">
  </el-col>
  <el-col :span="6" :offset="10">
    <div class="aname">
      {{info.vname }}
    </div>
  </el-col>
</el-row>      
      </div>
        <div class="text item">
          <b>性别：</b> {{info.gender}}
      </div>
        <div class="text item">
          <b>电话：</b> {{info.telephone}}
      </div>
        <div class="text item">
          <b>邮箱：</b> {{info.email}}
      </div>
        <div class="text item">
          <b>地址：</b> {{info.address}}
      </div>
</el-card>
  </div>
</template>

<script>
export default {
  data(){
    return {
      url: this.$store.getters.getAvator,
      info: {
      vname: '',
      address: '',
      email: '',
      gender: '',
      telephone: '',
      }
      
    }
  },
 mounted(){
    this.init()
    this.$bus.$on('receiveTx',this.receiveTx)
  },
  methods: {
    receiveTx(params){
      this.url = params
      // console.log('接收到头像',params);
    },
    init(){
      this.info.vname = this.$store.getters.getName
      this.info.address = this.$store.getters.getAddr
      this.info.email = this.$store.getters.getEmail
      this.info.gender = this.$store.getters.getGender
      this.info.telephone = this.$store.getters.getTel
    },
  }
}
</script>

<style lang="scss" scoped>
.avator {
  width: 150px;
  height: 150px;
  border-radius: 50px;
}
.aname {
  margin-top: 127px;
}
  .text {
    font-size: 25px;
    color: #deab8a;
    margin:35px 0;
  }

  .text:nth-child(1){
    font-size: 30px;
    text-align: center;
    margin :60px 0;
  }

  .item {
    padding: 18px 0;
  }

  .box-card {
    background-color: rgba(255, 255, 255, 0.76);
    backdrop-filter: blur(1px);
    border-radius: 20px;
    width: 480px;
  }
</style>