<template>
  <div class="topbar">
    <el-row type="flex" class="row-bg" justify="space-between">
  <el-col :span="20">
    {{path}}
  </el-col>
  <el-col :span="3">
     <span>管理员: </span>
    <span v-html="name"></span>
    
  
  <el-dropdown @command="handleCommand">
  <span class="el-dropdown-link">
    <img :src="url" class="default" alt="">
  </span>
  <el-dropdown-menu slot="dropdown">
    <el-dropdown-item command="a">修改头像</el-dropdown-item>
    <el-dropdown-item command="b">修改密码</el-dropdown-item>
    <el-dropdown-item command="c">退出登录</el-dropdown-item>
  </el-dropdown-menu>
</el-dropdown>

  </el-col>
</el-row>

    
  </div>
</template>

<script>
export default {
  components: {

  },
  data(){
    return {
      url: this.$store.getters.getAvator,
      name: '',
      path: '',
    }
  },
 mounted(){
    this.init()
    this.$bus.$on('receiveTx',this.receiveTx)
  },
  methods: {
    receiveTx(params){
      this.url = params
      // console.log('接收到头像',params);
    },
    init(){
      this.name = this.$store.getters.getName
    },
     handleCommand(command) {
      if(command=='a'||command=='b'){
        console.log('ab');
        this.$router.push('/infomg')
      }else{
        this.$store.commit('del_token')
        this.$message({
            type: 'success',
            message: '退出登录成功'
          });
        this.$router.push('/login')
      }
      }
  }
}
</script>

<style lang="scss" scoped>
  .topbar {
    color: #fffef9;
  }
  .default {
    width: 50px;
    height: 50px;
    border-radius: 20px;
    margin-left: 10px;
  }
</style>