<template>
  <div>

    修改头像
    <input type="file" name="file" class="fileBtn">
    <input type="submit" name="submit" value="上传" class="submitBtn" @click="submitVideo">
    <!-- <img class="avator" :src="url" alt=""> -->
    <upload></upload>
  </div>
</template>

<script>
import API from '../utils/service'
import Upload from './Upload.vue'
export default {
  components: {
    Upload
  },
  data() {
    return {
      url: require('@/upload/samoye.jpeg'),
      
    }
  },
  methods: {
    submitVideo() {
      let formData = new FormData();
      formData.append('multipartFile',document.querySelector('input[type=file]').files[0]);
      API({
        url: '/system/headerImgUpload',
        data: formData,
        method: 'post',
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then((res)=>{
        this.url = `http://localhost:9001/${res.data.data}`
        this.$store.commit('store_avator',this.url)
        // console.log(this.$store.getters.getAvator);
        this.$bus.$emit('receiveTx',this.url)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
   .avator{
      width: 100px;
   }
</style>