<!-- 使用elements-ui的上传组件 -->
<template>
  <div>
<el-upload
    class="upload-demo"
    ref="upload"
    action="/"
    :on-change="uploadFile"
    :auto-upload="false"
    :show-file-list="false">
    <el-button slot="trigger" size="small" type="primary">上传图片</el-button>
    <div slot="tip" class="el-upload__tip">请上传JPG、JPEG、PNG，大小不超过2M</div>
    </el-upload>
    
    <el-button @click.native="submitForm" type="primary">提交</el-button>
  </div>
    
</template>

<script>
import API from '../utils/service.js'
export default {
  data() {
      return {
       imgUrl: '',
      };
    },
    methods: {
       
   uploadFile(file) {
      this.imgUrl = file.raw // 拿到文件的信息
   },
   submitForm() {
    let formData = new FormData()
    formData.append('multipartFile', this.imgUrl)
    API({
        url: '/system/headerImgUpload',
        data: formData,
        method: 'post',
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then((res)=>{
        this.url = `http://localhost:9001/${res.data.data}`
        this.$store.commit('store_avator',this.url)
        // console.log(this.$store.getters.getAvator);
        this.$bus.$emit('receiveTx',this.url)
      })
},
 
/* 请求头
    headers: {
        'Content-Type': 'multipart/form-data'
      }
*/
