<template>
  <div>
    <el-row>
      <span @click="open">修改密码</span>  
     </el-row>
    
    <el-divider><i class="el-icon-mobile-phone"></i></el-divider>
     <el-row>
      修改头像
     </el-row>
     <el-row>
      <!-- <TX /> -->
        <user-upload></user-upload>
     </el-row>
  </div>
     

</template>

<script>
import API from '../utils/service.js'
import UserUpload from './UserUpload.vue'
export default {
  components: {
    UserUpload
  },
  data() {
    return {
      oldPwd: '',
      newPwd: ''
    }
  },
  mounted() {
    
  },
  methods: {
    open() {
        this.$prompt('请输入旧密码',  {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({ value }) => {
          this.oldPwd = value;
          this.$message({
            type: 'success',
            message: '输入密码成功'
          });

        this.$prompt('请输入新密码',  {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({ value }) => {
          this.newPwd = value
          this.$message({
            type: 'success',
            message: '输入密码成功'
          });
        const token = this.$store.getters.get_token
        API({
          url: `/system/updatePwd/${this.oldPwd}/${this.newPwd}`,
         headers: {
          token
        },
          method: 'post'
        }).then((res)=>{
          console.log(res);
        })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });       
        });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });       
        });
      },

     

 

  }
   
}
</script>

<style lang="scss" scoped>
  .el-row {
    margin: 50px 0;
    color: #ae6642;
    font-size: 30px;
    text-align: center;
  }
</style>