<template>
  <div class="container">
   <el-tag
  :key="tag"
  v-for="tag in dynamicTags"
  closable
  :disable-transitions="false"
  @close="handleClose(tag)"
  >
  <span @click="link">{{tag}}</span>
  
</el-tag>
  </div>
</template>

<script>
export default {
 data() {
      return {
        dynamicTags: [],
        path: '',
        index: {
          '首页': '/main',
          '学生管理': '/studentadmin',
          '课程表': '/course',
          '班级管理': '/class',
          '教师管理': '/teacher',
          '班级名单': '/classlist',
          '通讯录': '/maillist',
          '年级管理': 'grade',
          '管理员管理': '/manager',
          '个人信息': '/infomg',
          '奖学金信息': '/jiang',
          '助学金信息': '/zhu',
          '一卡通信息': '/card',
          '我的成绩': '/score'
        }
      };
    },
    mounted(){
      this.$bus.$on('receiveParams',this.receiveParams)
    },
    methods: {
    handleClose(tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
    },
    receiveParams(params){
      console.log('接收到的参数',params);
      console.log(this.dynamicTags);
      let res = this.dynamicTags.indexOf(params)
      if(res!=-1){
        // console.log('存在');
      }else{
        this.dynamicTags.push(params)
      }
    },
    link(e){
      let index = e.target.innerHTML
      let value = this.index[index]
      this.$router.push(value)
    }
    },
}
</script>

<style>
   .el-tag {
    width: 120px;
    height: 50px;
    margin: 0 10px 20px 0;
    background-color: #FDF6EC;
    font-size:15px;
    line-height: 50px;
    text-align: center;
    color: #E6A23C;
    border-color: #FDF6EC;
  }
</style>