import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import Lm from "./components/LeftMenu.vue"
import Rt from "./components/RightTop.vue"
import IC from './components/InfoCard.vue'
import CI from './components/ChangeInfo.vue'
import TX from './components/toux.vue'
import NV from './components/NavBar.vue'
import 'vue2-animate/dist/vue2-animate.min.css'

Vue.config.productionTip = false
Vue.component('Lm', Lm)
Vue.component('Rt', Rt)
Vue.component('IC', IC)
Vue.component('CI', CI)
Vue.component('TX', TX)
Vue.component('NV',NV)
Vue.use(ElementUI);

new Vue({
  router,
  store,
  beforeCreate() {
    Vue.prototype.$bus = this;
  },
  render: h => h(App)
}).$mount('#app')
