const app = {
  state: {
  
  },
  getters: {
    get_token() {
      return localStorage.getItem('token')
    }
  },
  mutations: {
    // 设置token
  set_token(state, token) {
    state.token = token;
    localStorage.setItem('token', token);
    },
  del_token(state) {
    state.token = '';
    localStorage.removeItem('token');
    },
    
  }
}

export default app