import Vue from 'vue'
import Vuex from 'vuex'
import app from './modules/app'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    avator: require('@/upload/tx1.jpg'),
    name: '',
    address: '',
    email: '',
    gender: '',
    telephone: '',
  },
  getters: {
    getAvator(state) {
      return state.avator
    },
    getName(state) {
      return state.name
    },
    getAddr(state) {
      return state.address
    },
    getEmail(state) {
      return state.email
    },
    getGender(state) {
      return state.gender
    },
    getTel(state) {
      return state.telephone
    },
  },
  mutations: {
    store_info(state, info) {
      state.name = info.name
      state.address = info.address
      state.email = info.email
      state.gender = info.gender
      state.telephone = info.telephone
    },
    store_avator(state, info) {
      state.avator = info
    }
  },
  actions: {
  },
  modules: {
    app
  },
  plugins: [createPersistedState()]
})
