<template>
  <div>
    <!-- 用户头像上传 -->
    
    <el-upload
  class="avatar-uploader"
  action="/"
  :show-file-list="false"
  :on-change="uploadFile"
  :on-progress="submitForm">
  <img v-if="imageUrl" :src="imageUrl" class="avatar">
  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
</el-upload>
  

    
  </div>
</template>

<script>
import API from '../utils/service.js'
export default {
 data() {
      return {
        imageUrl: ''
      };
    },
    methods: {
      uploadFile(file) {
      this.imgUrl = file.raw // 拿到文件的信息
   },
   submitForm() {
    let formData = new FormData()
    formData.append('multipartFile', this.imgUrl)
    API({
        url: '/system/headerImgUpload',
        data: formData,
        method: 'post',
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then((res)=>{
        this.imageUrl = `http://localhost:9001/${res.data.data}`
        this.$store.commit('store_avator',this.imageUrl)
        // console.log(this.$store.getters.getAvator);
        this.$bus.$emit('receiveTx',this.imageUrl)
      })
   }
   },
}
</script>

<style lang="scss">
 .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #deab8a;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>